.search-date-picker {
  position: relative;
  display: flex;
  justify-content: center;
}

.search-date-picker-cont {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.search-date-picker-sub-cont{
  margin-bottom: 10px;
}

.guest-section-search {
  width: 40%;
  display: flex;
  flex-direction: column;
  padding-left: 17.5px;
}

.guest-count-label {
  display: flex;
  justify-content: space-between;
  padding-left: 0.04rem;
  padding-right: 0.15rem;
  line-height: 1.2rem;
}

#guest-count {
  border: 0;
  outline: 0;
  font-size: 12.5px;
}

.date-pick-btn {
  color: white;
  width: 100%;
  border-radius: 0;
  margin-top: 0;
}

.date-pick-btn:hover {
  transform: none;
}

.rdrDayToday .rdrDayNumber span::after {
  background-color: var(--primary-color) !important;
}

@media screen and (max-width: 600px) {
  .guest-section-search {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    flex-direction: row;
    justify-content: space-between;
    margin-top: -10px;
  }

  .guest-count-label {
    width: 70%;
  }

  .guest-count-label > p {
    margin-bottom: 0;
  }

  #guest-count {
    width: 20%;
  }

  .rdrDefinedRangesWrapper {
    display: none;
  }
}

@media screen and (max-width: 350px) {
  .rdrCalendarWrapper {
    width: 290px;
  }
  .rdrMonthsVertical {
    flex-direction: row !important;
  }
}
