.cards {
  transition: transform 0.2s;
  cursor: pointer;
}

.cards:hover {
  transform: scale(1.025);
}

.card-info {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
}

.card-description {
  margin-top: 0.2rem;
  margin-bottom: 0;
}

.card-price {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.card-img {
  border-radius: 0.4rem;
  width: 100%;
  height: 17rem;
  object-fit: cover;
}

.review-info{
  display: flex;
}

.star-icon{
  margin-left: 4px;
  color: var(--primary-color);
}

@media screen and (max-width: 1600px) and (min-width: 1150px) {
  .row-cols-1 > * {
    flex: 0 0 auto;
    width: 25%;
  }
}

@media screen and (max-width: 1150px) and (min-width: 900px) {
  .row-cols-1 > * {
    flex: 0 0 auto;
    width: 33.33%;
  }
}

@media screen and (max-width: 900px) and (min-width: 550px) {
  .row-cols-1 > * {
    flex: 0 0 auto;
    width: 50%;
  }
}

@media screen and (max-width: 550px) and (min-width: 0px) {
  .row-cols-1 > * {
    flex: 0 0 auto;
    width: 100%;
  }
}
