.category-section {
  padding: 0 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.cat-sec-main {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.nav-ul-parent {
  width: 95%;
  overflow-x: hidden;
}

.category-sec-ul {
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  flex-wrap: nowrap;
  width: 100%;
}

.category-subsec {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.category-sec-ul .active {
  color: var(--bs-body-color);
}

.category-sec-ul .active::after {
  top: 100%;
  position: absolute;
  width: 100%;
  content: " ";
  border-bottom: 2px solid var(--bs-body-color);
}

.cat-item {
  min-width: 100px;
}

.category-image > img {
  width: 35px;
}

.category-name {
  font-size: 14px;
}

.cat-btn {
  border: none;
  background-color: var(--bs-body-bg);
  font-size: larger;
}

@media screen and (max-width: 1200px) {
  .category-section {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .cat-item {
    min-width: 90px;
  }

  .category-image > img {
    width: 30px;
  }

  .category-name {
    margin-top: 5px;
    font-size: 12.5px;
  }

  .cat-btn {
    font-size: large;
  }
}

@media screen and (max-width: 750px) {
  .category-section {
    padding: 0 10px;
    margin-top: 2px;
    margin-bottom: 2px;
  }

  .cat-item {
    min-width: 75px;
  }

  .category-image > img {
    width: 25px;
  }

  .category-name {
    margin-top: 2px;
    font-size: 12px;
  }

  .cat-btn {
    font-size: medium;
    padding: 0;
  }
}
