.home {
  position: relative;
}

.home-subsec {
  padding: 4rem 5rem 6rem 5rem;
}

.cards-common {
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.sm-btn {
  position: fixed;
  bottom: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.map-toggle-btn{
    background: var(--bs-body-color);
    border: 1px solid var(--bs-body-color);
    color: white;
}

@media screen and (max-width: 1250px) {
  .home-subsec {
    padding: 4rem 2.5rem;
  }
}

@media screen and (max-width: 750px) {
  .home-subsec {
    padding: 4rem 2rem;
  }
}
