.header {
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}

.navbar-logo {
  object-fit: contain;
  width: 120px;
}

.header-left{
  width: 30%;
}

.header-center {
  padding: 7px;
  border: 1px solid lightgray;
  border-radius: 99px;
}

.header-right {
  align-items: center;
  cursor: pointer;
  width: 30%;
  justify-content: flex-end;
}

@media screen and (max-width: 750px) {
  .header-left,
  .header-right {
    display: none !important;
  }

  .header-center {
    width: 100%;
  }
}
