.search-page {
  display: flex;
  position: relative;
}

.search-results {
  width: 50%;
}

.home-subsection {
  padding: 10px 25px 70px 25px;
}

.sp-text{
  padding: 20px 40px 10px 40px;
}

.no-sr > h2 {
  margin-bottom: 15px;
}

.g-map {
  width: 50%;
  height: calc(100vh - (113.45px + 45px));
  position: fixed;
  right: 0;
}

.g-map-style {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1250px) {
  .search-page {
    flex-direction: column-reverse;
  }

  .search-results {
    width: 100%;
  }

  .g-map {
    width: 100%;
    height: 40vh;
    position: relative;
  }

  .g-map-style {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 1250px) and (min-width: 900px) {
  .row-cols-sr > * {
    flex: 0 0 auto;
    width: 33.33%;
  }
}

@media screen and (max-width: 900px) and (min-width: 550px) {
  .row-cols-sr > * {
    flex: 0 0 auto;
    width: 50%;
  }
}

@media screen and (max-width: 550px) and (min-width: 0px) {
  .row-cols-sr > * {
    flex: 0 0 auto;
    width: 100%;
  }
}
