.card-inside-cont {
  padding: 25px 20px 50px 20px;
}

.custom-my {
  margin-top: 25px;
  margin-bottom: 25px;
}

.card-inside-images {
  display: grid;
  grid-template-areas:
    "main I III"
    "main II IV";
  grid-template-columns: auto auto auto;
  border-radius: 10px;
  overflow: hidden;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.card-inside-image {
  width: 100%;
  height: 10rem;
  object-fit: cover;
}

#card-inside-image-main {
  grid-area: main;
  height: calc(20rem + 10px) !important;
}

#card-inside-image-I {
  grid-area: I;
}

#card-inside-image-II {
  grid-area: II;
}

#card-inside-image-III {
  grid-area: III;
}

#card-inside-image-IV {
  grid-area: IV;
}

.card-inside-more-details {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.card-inside-checkout-tab {
  border: 2px solid #c4c4c4;
  border-radius: 10px;
  width: 40%;
  padding: 25px;
  height: 100%;
}

.card-inside-price {
  display: flex;
}

.card-inside-dates {
  display: grid;
  grid-template-areas:
    "ci co"
    "guests guests";
  border: 0.2px solid #c4c4c4;
  border-radius: 0.4rem;
}

.cog {
  border: 0.2px solid #c4c4c4;
  padding: 5px 10px;
}

.cog > p {
  margin: 0;
}

.card-inside-dates-ci {
  grid-area: ci;
}

.card-inside-dates-co {
  grid-area: co;
}

.card-inside-guest-sec {
  grid-area: guests;
}

#card-inside-guest-count {
  outline: 0;
  border: 0;
  width: 100%;
  background-color: transparent;
}

.card-inside-reserve-btn {
  width: 100%;
  border-radius: 0.4rem;
}

.card-inside-others {
  width: 55%;
}

.card-inside-becount-details {
  border: 1px solid #c4c4c4;
  border-radius: 0.4rem;
  padding: 25px;
  margin: 25px 0;
  width: 150px;
}

.card-inside-g-map {
  width: 100%;
  height: 70vh;
}

@media screen and (max-width: 770px) {
  .card-inside-location {
    margin: 25px 0;
  }

  .card-inside-more-details {
    display: block;
  }

  .card-inside-checkout-tab {
    width: 100%;
    margin-bottom: 40px;
  }

  .card-inside-others {
    width: 100%;
  }

  .card-inside-g-map {
    width: 100%;
    height: 60vh;
  }
}

@media screen and (max-width: 600px) {
  .card-inside-image {
    height: 15rem;
  }
}

@media screen and (max-width: 390px) {
  .card-inside-becount-details {
    width: 100px;
    padding: 15px;
  }
}
