.footer {
  background: white;
}

.footer-hr {
  opacity: 0.2;
  margin: 0;
}

.footer > p,
.footer > a {
  padding: 10px 20px;
  margin: 0 10px;
}

.fp-left {
  float: left;
}

.fp-right {
  float: right;
}

@media screen and (max-width: 750px) {
  .fp-left-cr {
    display: none;
  }

  .footer > a {
    display: block !important;
  }
}

@media screen and (max-width: 360px) {
  .footer > p {
    padding: 10px 5px;
  }
}
